// utils/videoProcessing.js
import fs from 'fs';
import path from 'path';
import ytdl from 'ytdl-core';
import { spawn } from 'child_process';

export async function downloadVideo(videoUrl, outputPath) {
    try {
        console.log('Initiating video download from:', videoUrl);

        // Check if it's a YouTube URL
        if (videoUrl.includes('youtu.be') || videoUrl.includes('youtube.com')) {
            // Download using ytdl-core for YouTube videos
            return new Promise((resolve, reject) => {
                const writeStream = fs.createWriteStream(outputPath);

                ytdl(videoUrl, {
                    quality: 'lowest', // Using lowest quality for faster processing
                    filter: 'audioonly', // We only need audio for transcription
                })
                    .pipe(writeStream)
                    .on('finish', () => {
                        console.log('Video download completed successfully');
                        resolve(true);
                    })
                    .on('error', (error) => {
                        console.error('Error downloading video:', error);
                        reject(error);
                    });
            });
        } else {
            // For non-YouTube URLs, use the existing axios implementation
            // ... (keep your existing axios implementation here)
        }
    } catch (error) {
        console.error('Error in downloadVideo:', error);
        throw new Error(`Failed to download video: ${error.message}`);
    }
}

export async function transcribeVideo(videoPath) {
    console.log('Starting transcription for video:', videoPath);

    return new Promise((resolve, reject) => {
        const pythonScript = path.join(process.cwd(), 'transcribe_video.py');
        const pythonProcess = spawn('python', [pythonScript, videoPath], {
            // Add these options to ensure proper Python environment
            env: { ...process.env, PYTHONIOENCODING: 'utf-8' },
            stdio: ['pipe', 'pipe', 'pipe'],
        });

        let transcriptData = '';
        let errorData = '';

        pythonProcess.stdout.on('data', (data) => {
            const output = data.toString();
            console.log('Python output:', output);
            transcriptData += output;
        });

        pythonProcess.stderr.on('data', (data) => {
            const error = data.toString();
            console.error('Python error:', error);
            errorData += error;
        });

        pythonProcess.on('close', (code) => {
            console.log('Python process exited with code:', code);

            if (code === 0 && transcriptData.trim()) {
                resolve(transcriptData.trim());
            } else {
                reject(new Error(`Transcription failed: ${errorData || 'No transcript generated'}`));
            }
        });
    });
}
