// pages/api/transcribe.js
import { downloadVideo, transcribeVideo } from '../../utils/videoProcessing';
import path from 'path';
import fs from 'fs';

export const config = {
    api: {
        bodyParser: {
            sizeLimit: '500mb'
        }
    }
};

export default async function handler(req, res) {
    if (req.method !== "POST") {
        return res.status(405).json({ error: "Method not allowed" });
    }

    try {
        const { videoUrl } = req.body;
        if (!videoUrl) {
            return res.status(400).json({ error: "No video URL provided" });
        }

        // Create temp directory if it doesn't exist
        const tempDir = path.join(process.cwd(), 'temp');
        if (!fs.existsSync(tempDir)) {
            fs.mkdirSync(tempDir, { recursive: true });
        }

        // Create unique filename for temp storage
        const tempFileName = `video-${Date.now()}.mp4`;
        const tempVideoPath = path.join(tempDir, tempFileName);

        console.log('Starting video download from:', videoUrl);

        try {
            await downloadVideo(videoUrl, tempVideoPath);

            console.log('Starting transcription...');
            const transcript = await transcribeVideo(tempVideoPath);

            // Clean up
            try {
                fs.unlinkSync(tempVideoPath);
            } catch (cleanupError) {
                console.error('Error cleaning up temp file:', cleanupError);
            }

            return res.status(200).json({ transcript });
        } catch (processingError) {
            // Clean up on error
            if (fs.existsSync(tempVideoPath)) {
                try {
                    fs.unlinkSync(tempVideoPath);
                } catch (cleanupError) {
                    console.error('Error cleaning up temp file after failure:', cleanupError);
                }
            }
            throw processingError;
        }
    } catch (error) {
        console.error('Error in transcribe API:', error);
        return res.status(500).json({
            error: error.message,
            details: process.env.NODE_ENV === 'development' ? error.stack : undefined
        });
    }
}